<template>
    <div class="item">
        <h5><img src="../static/img/neixunhuan.png" class="left">
            <span>新风比</span>
        </h5>
        <div class="box">
            <div  v-for="(item,index) in xunhuanList" :class="[xunhuan,timexunhuan,current===index ? selected:'']" @click="select(index)">
               {{item}}
            </div>
           
        </div>
    </div>
</template>
<script>
export default {
    name:"xinfengbi",
    data(){
        return {
            xunhuan:"xunhuan",
            selected:"selected",
            timexunhuan:"timexunhuan",
            current:"",
            xunhuanList:["0%","25%","50%","75%","100%"]
        }
    },
     methods: {
    select(target) {
      this.current = target;
    }
  }
}
</script>



