<template>
  <div class="item" style="margin-top:0.3rem;">
				<h5><img src="../static/img/moshi.png" class="left"><span>模式</span></h5>
				<div class="box">
					<div class="it close" :class="{selected:target==='close'}" @click="target='close'">
						关机
					</div>
				
					<div class="it auto" :class="{selected:target==='auto'}" @click="target='auto'">自动</div>
					
					<div class="it shoudong" :class="{selected:target==='shoudong'}" @click="target='shoudong'">
						手动
					</div>
				</div>
			</div>
</template>
<script>
export default {
  name: "workMode",
  props: ["which", "mode"],
  data() {
    return {
      parent: "",
      transData: this.mode,//这种其实是 = 赋值的意思，只会在组件构建完成时执行一次
      close: false,
      auto: false,
      shoudong: false,
      target:''
    };
  },
  methods: {
    judge: function() {
      // het.toast("uuuuuu")
      if (this.parent == "general") {//普通模式
        // het.toast("普通模式ppppp");
        console.log(this.mode.co2Value);
        if (this.mode.machineOnOff == 1) {//关机
          this.target = 'close';
        } else if (this.mode.machineOnOff == 7) {
          //自动模式
          this.target = 'auto';
        } else {//手动模式
          this.target = 'shoudong';
        }
      }
    }
  },
  watch: {
    mode: function(newval, oldval) {
      this.judge();
    }
  },
  mounted() {
    this.parent = this.which;
      this.judge();
    
  },
   beforeCreate(){
    console.log("zhe shi beforeCreate")
  },
   created(){
    console.log("zhe shi created")
  },
   beforeMount(){
    console.log("zhe shi beforeMount")
  },
   beforeUpdate(){
    console.log("zhe shi beforeUpdate")
  },
   updated(){
    console.log("zhe shi updated")
  },
   beforeDestroy(){
    console.log("zhe shi beforeDestroy ")
  },
   destroyed(){
    console.log("zhe shi destroyed")
  },
};
</script>



