

		

<template>
	<div id="wrap">
		<input id="demo3" type="text" readonly="" name="input_date" placeholder="时间选择特效" style="display: none;" />

		<div class="header">
			<img src="../static/img/back_2.png" class="back" />

			<span>设置</span>

			<div class="kong">

			</div>
		</div>
		<div class="tab">
			<div class="general" @click="general" ><span :class="{selected:isGeneral}">普通模式</span></div>
			<div class="timing" @click="timing" ><span :class="isGeneral ? '' : 'selected'">定时模式</span></div>
		</div>
    <router-view/>
	</div>
</template>
 <style lang="scss"  src='../static/css/Config.scss'>
</style>

<script>
// import "../static/css/Config.scss";
import "../static/css/lCalendar.css";
export default {
  name: "config",
  data() {
    return {
      isGeneral: true
    };
  },

  methods: {
    general: function() {
      this.$router.replace({ path: "/Config/generalDiv" });
      this.isGeneral = true;
    },
    timing: function() {
      this.isGeneral = false;
      this.$router.replace({ path: "/Config/timingDiv" });
    }
  },
  mounted: function() {
    this.general();
    het.domReady(function() {
      console.log("ready777");
      	het.config({
        torporTime: 0,
        onceConfigData: false,
        renderConfigData: true,
        debugMode: "print", // 打印调试数据
        updateFlagMap: {}
      })
      
    });
//     het.repaint(function(data, type) {
//         console.log("repaint");
//         console.log(data)
// console.log(JSON.stringify(data))
//         het.toast("repaint");
//         console.log("接收到数据config页面", data);
//         console.log("数据类型为", type === 0 ? "控制数据" : "运行数据");
//       });
  }
};
</script>




