

<template>
	<div id="wrap">
		<div class="up" :style="{'background-image':bgImg}">
			<div class="header">
				<h6>
					<img src="../static/img/back.png" class="back">赫为新风机YUBHM（共享）</h6>
			</div>
	
			<div class="center_data">
	
				<div class="center_data_div quarter1">
	
					<img src="../static/img/cloud.png" class="cloud" />
	
				</div>
	
				<div class="center_data_div quarter2">

	
					<div class="quarter2_temperature">
	
						<img src="../static/img/wendu.png" />
	
						<span class="tempValue value">{{tempValue}}</span>
	
						<span class="unit">℃</span>
	
					</div>
	
					<div class="quarter2_humidity">
	
						<img src="../static/img/shidu.png" />
	
						<span class="humValue value">{{humValue}}</span>
	
						<span class="unit">％</span>
	
					</div>
	
					<div class="quarter2_3_PM25">
	
						<img src="../static/img/pm.png" />
	
						<span class="pm25Value value">{{pm25Value}}</span>
	
						<span class="unit">μg/m³</span>
	
					</div>
	
					<div class="quarter2_grade">
	
						<img src="../static/img/dengji.png" />
	
						<span class="grade value">{{gradeText}}</span>
	
						<span class="unit">级</span>
	
					</div>
	
					<div class="quarter2_text" style="margin-left: 0;">
	
						<span>空气质量：</span>
	
						<span class="weather" >{{weatherText}}</span>
	
					</div>
	
				</div>
	
				<div class="center_data_div quarter3">
	
					<h5>室内
	
						<span>PM2.5</span>
	
					</h5>
	
					<h4>
						<span class="pm25Value">{{pm25Value}}</span>μg/m³</h4>
				</div>
	
				<div class="center_data_div quarter4">
	
					<h5>室内
						<span>CO₂</span>
					</h5>
					<h4>
						<span class="co2Value">{{co2Value}}</span>ppm</h4>
				</div>
	
			</div>
	
			<div class="startButton">
	
				<img  class="switch" v-bind:src="Open ? switchOpen:switchClose" @click="changeSwitch"/>
	
			</div>
	
			<div class="menu">
	
				<div class="menu_div" @click="switchAUTO">
	
					<img  class="auto" :src="autoActive ? autoDown:autoUp" />
	
					<h6 :class="{active:autoActive}">自动</h6>
	
				</div>
	
				<div class="menu_div" @click="toConfig">
	
					<img src="../static/img/shezhi.png" class="config" />
	
					<h6 >设置</h6>
	
				</div>
	
				<div class="menu_div" @click="switchEMF">
	
					<img  class="EMF" :src="EMFActive ? emfDown:emfUp"/>
	
					<h6 :class="{active:EMFActive}">EMF</h6>
	
				</div>
	
				<div class="menu_div" @click="refresh">
	
					<img src="../static/img/shuaxin.png" class="shuaxin" />
	
					<h6>刷新</h6>
	
				</div>
	
			</div>
	
		</div>
		<div class="down">
			<div class="buttom">
				<h6>
					<span class="left">滤网剩余时间</span>
					<span class="right filterNetPercent">{{filterNetPercent}}</span>
				</h6>
			</div>
	
			<div class="bar">
	
				<div class="bar_color" :style="{width:bar_color_width}">
	
					<img src="../static/img/lvwang.png" class="lvwang" />
	
				</div>
	
			</div>
	
		</div>
	
	</div>
</template>
<script>
import "../static/css/index.scss";

console.log(het);
het.setTitle("控制面板");

export default {
  name: "index",
  data() {
    return {
      Open: false,
      autoActive: false,
      EMFActive: false,
      switchClose: require("../static/img/close.png"),
      switchOpen: require("../static/img/open.png"),
      emfUp: require("../static/img/emfUp.png"),
      emfDown: require("../static/img/emfDown.png"),
      autoDown: require("../static/img/autoDown.png"),
      autoUp: require("../static/img/autoUp.png"),
      gradeText: "--",
      weatherText: "--",
      bgImg: "--",
      bgImg1: "url(" + require("../static/img/bg_01.png") + ")",
      bgImg2: "url(" + require("../static/img/bg_02.png") + ")",
      bgImg3: "url(" + require("../static/img/bg_03.png") + ")",
      bgImg4: "url(" + require("../static/img/bg_04.png") + ")",
      bgImg5: "url(" + require("../static/img/bg_05.png") + ")",
      bgImg6: "url(" + require("../static/img/bg_06.png") + ")",
      co2Value: "--",
      pm25Value: "--",
      tempValue: "--",
      humValue: "--",
      filterNetPercent: "%",
      bar_color_width: "0"
    };
  },
  methods: {
    toConfig: function() {
      this.$router.push({ path: "/Config" });
    },
    changeSwitch: async function() {
      this.Open = !this.Open;
      if (this.Open) {
        het.send(
          {
            machineOnOff: "2",
            updateFlag: het.hexUpFlag(13, 1, 4)
          },
          function() {
            this.Open = true;
            console.log("开机成功");
            het.toast("开机成功");
          },
          function() {
            this.Open = false;
            console.log("开机失败");
            het.toast("开机失败");
          }
        );
      } else {
        het.send(
          {
            machineOnOff: "1",
            updateFlag: het.hexUpFlag(13, 1, 4)
          },
          function() {
            console.log("关机成功");
            het.toast("关机成功");
            this.Open = false;
          },
          function() {
            this.Open = true;
            console.log("关机失败");
            het.toast("关机失败");
          }
        );
      }
    },
    switchEMF: function() {
      //开关EMF功能
      this.EMFActive = !this.EMFActive;

      if (this.EMFActive) {
        het.send(
          {
            EMF: "2",
            updateFlag: het.hexUpFlag(2, 1, 4)
          },
          function() {
            // het.toast("EMF开启成功");
            this.EMFActive = true;
          },
          function() {
            this.EMFActive = false;
            het.toast("EMF开启失败");
          }
        );
      } else {
        het.send(
          {
            EMF: "1",
            updateFlag: het.hexUpFlag(2, 1, 4)
          },
          function() {
            // het.toast("EMF关闭成功");
            this.EMFActive = false;
          },
          function() {
            this.EMFActive = true;
            het.toast("EMF关闭失败");
          }
        );
      }
    },
    switchAUTO: function() {
      //开关自动模式功能
      this.autoActive = !this.autoActive;

      if (this.autoActive) {
        het.send(
          {
            workMode: "7",
            updateFlag: het.hexUpFlag(0, 1, 4)
          },
          function() {
            // het.toast("自动模式开启成功");
            this.autoActive = true;
          },
          function() {
            this.autoActive = false;
            het.toast("自动模式开启失败");
          }
        );
      } else {
        het.send(
          {
            workMode: "2",
            freshAirRatio: 5,
            updateFlag: het.hexUpFlag(0, 1, 4, het.hexUpFlag(1, 1, 4))
          },
          function() {
            // het.toast("EMF关闭成功");
            this.autoActive = false;
          },
          function() {
            this.autoActive = true;
            het.toast("EMF关闭失败");
          }
        );
      }
    },
    refresh: function() {
      location.reload();
    }
  },
  mounted: function() {
    var vm = this;
    var wgtVer = null;
    document.addEventListener("plusready", function() {
      plus.runtime.getProperty(plus.runtime.appid, function(inf) {
        wgtVer = inf.version;
        console.log("当前应用版本：" + wgtVer);
        wgtVer = wgtVer + "";
      });
      //  	plus.nativeUI.showWaiting();
      console.log(plus.webview.currentWebview().id);
    });
    /**@augments
     * 第三方库het的准备工作,vue 的热加载不会刷新domReady方法
     */
    het.domReady(function() {
      var pm25Value;
      het.config({
        torporTime: 0,
        onceConfigData: false,
        renderConfigData: true,
        debugMode: "print", // 打印调试数据
        updateFlagMap: {}
      });

      /**@augments
       * 原生APP推过来数据
       */
      het.repaint(function(data, type) {
        console.log("接收到数据首页", data);
        console.log("数据类型为", type === 0 ? "控制数据" : "运行数据");
        // 如果type==0则为控制数据
        if (type == 0) {
          // het.toast("控制数据");
          //			判断当前是关机还是开机
          if (data.machineOnOff == 2) {
            vm.Open = true;
          } else if (data.machineOnOff == 1) {
            vm.Open = false;
          }
          //			判断EMF是开启还是未开启
          if (data.EMF == 1) {
            vm.EMFActive = false;
          } else if (data.EMF == 2) {
            vm.EMFActive = true;
          }
          //			判断是否是自动模式
          if (data.workMode == 7) {
            vm.gradeText = data.fanSpeed - 1;
            vm.autoActive = true;
          } else {
            vm.autoActive = false;
          }
          if (data.fanSpeed != 0) {
            vm.gradeText = data.fanSpeed - 1;
          } else {
            vm.gradeText = "--";
          }
        } else {
          // 运行数据
          if (data.pm25Value != undefined) {
            pm25Value = data.pm25Value;
            if (pm25Value >= 0 && pm25Value < 35) {
              vm.weatherText = "优";
              vm.bgImg = vm.bgImg1;
            } else if (pm25Value >= 35 && pm25Value < 75) {
              vm.weatherText = "良";
              vm.bgImg = vm.bgImg2;
            } else if (pm25Value >= 75 && pm25Value < 115) {
              vm.weatherText = "轻度污染";
              vm.bgImg = vm.bgImg3;
            } else if (pm25Value >= 115 && pm25Value < 150) {
              vm.weatherText = "中度污染";
              vm.bgImg = vm.bgImg4;
            } else if (pm25Value >= 150 && pm25Value < 250) {
              vm.weatherText = "重度污染";
              vm.bgImg = vm.bgImg5;
            } else if (pm25Value >= 250 && pm25Value < 500) {
              vm.weatherText = "严重污染";
              vm.bgImg = vm.bgImg6;
            }
          }
          if (data.co2Value != undefined) {
            vm.co2Value = data.co2Value;
          } else {
            vm.co2Value = "--";
          }
          if (data.co2Value != undefined) {
            vm.pm25Value = data.pm25Value;
          } else {
            vm.pm25Value = "--";
          }
          if (data.tempValue != undefined) {
            vm.tempValue = data.tempValue;
          } else {
            vm.tempValue = "--";
          }
          if (data.humValue != undefined) {
            vm.humValue = data.humValue;
          } else {
            vm.humValue = "--";
          }
          if (data.filterNetPercent != undefined) {
            vm.filterNetPercent = data.filterNetPercent + "%";
            vm.bar_color_width = data.filterNetPercent + "%";
          }

          //		het.send({
          //			machineOnOff: 1,
          //			updateFlag: het.hexUpFlag(0, 1, 4, het.hexUpFlag(3, 1, 4))
          //		}, function() {
          //			console.log("chengong")
          //		}, function() {
          //			console.log("shibai")
          //		})
        }
      }); //原生APP推送数据结束
    }); //het.dowReady结束
  }
};
</script>



