<template>
  <div class="item timegears">
    <h5><img src="../static/img/dangwei.png" class="left">
      <span>档位</span>
    </h5>
    <div class="box">
      <div :class="[dangwei,timedangwei,current===index ? selected : '']" v-for="(item,index) in items" @click="select(index)">
        {{item}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "timegears",
  props:["someProps"],
  data() {
    return {
      dangwei: "dangwei",
      timedangwei: "timedangwei",
      selected: "selected",
      items: [1, 2, 3, 4, 5],
      current: ""
    };
  },
  methods: {
    select(target) {
      this.current = target;
    }
  },
  mounted(){
    console.log(this.someProps)

    console.log(typeof(this.someProps))
  }
};
</script>




