import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import index from '@/page/index'
import Config from '@/page/Config'
import generalDiv from '@/page/generalDiv'
const timingDiv=() => import('@/page/timingDiv')
Vue.use(Router)

export default new Router({
  // mode:"history",
  routes: [
    {
      path: '/',
      name: 'index',//控制面板首页
      component: index
    },
    {
      path: '/Config',
      name: 'Config',//设置页面
      component: Config,
      children: [
        {
          path: 'timingDiv',//设置页面定时模块
          component: timingDiv
        },
        {
          path: 'generalDiv',//设置页面普通操作模块
          component: generalDiv
        }
      ]
    }
  ]
})



